body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.home {
  max-width: 700px;
  color: #20B887;
}

.mode-title-word-home {
  font-size: 100px;
  font: Roboto;
  line-height: 169px;
  align-items: center;
  text-align: center;
  margin-top: 100px;

  color: #20B887;
}

.mode-title-word-description-home {
  font-weight: 200;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 140px;
  color: #036344;
}

.mode-wraper-home {
  display: grid;
  align-items: start;
  max-width: 100%;
  height: 100%;
  justify-content: center;
  align-content: center;
  row-gap: 10px;
  text-align: center;
}

.nav {
  max-width: 305px;
  height: 1024px;
  left: 0px;
  top: 0px;

  background: #20B887;
}

.nav-container a {
  padding: 15px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  text-decoration: none;
  color: white;
}

.nav-container {
  display: grid;
  row-gap: 30px;
  list-style-type: none;
  padding: 250px 0 0 0;
  margin: 0;

  color: white;
  
}

ul > a.active {
  background: #F9FBFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: #20B887;

  padding: 15px;
}

.logo-container {
  display: grid;
  grid-auto-flow: column;
  padding: 30px;
  align-items: center;
  column-gap: 15px;
}

.level-title {
  font-style: normal;
  font-weight: 900;
  font-size: 36px;
  line-height: 42px;
  white-space: nowrap;
  
  color: #6FF3CB;
}

.btn-round {
  cursor: pointer;
  width: 54px;
  height: 54px;
  margin-right: 30px;  
  margin-bottom: 30px;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  border-radius: 50%;
  box-shadow: 0px 2px 2px 0px rgba(0,0,0, 0.50);
}

.btn-round.add, .btn-round.close{
  background-image: url("./img/add.svg");
  background-position: center;
  background-repeat: no-repeat;
  transition: .2s linear;
}

.close {
  transform: rotate(45deg);
}

.btn-round.check {
  background-image: url("./img/check.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.app-wraper {
  display: flex;
}

.btn-round:focus {
  outline: none;
}

