} */

.add_word_container, .add_word_container > div {
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  column-gap: 20px;
}

.add_word_container {
  margin-top: 20px;
  display: flex;
  justify-content: flex-end;
  
}

.add_word_container input{
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  padding: 8px 2px;

  color: rgba(0, 0, 0, 0.3);
}

.in-put::placeholder {color: #000000;}

.add_word_container input, textarea {
  outline: none;
  border: none;
  border-bottom: 1px solid #000000;
}

.page-container {
  display: grid;
  justify-content: center;
  grid-auto-flow: row;
  align-items: start;
  align-content: flex-start;
  column-gap: 60px;
}

.learn-container {
  position: relative;
  height: 400px;
  background: rgba(242, 246, 254, 0.46);
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
  text-align: center;
}

.learn-wrapper {
  width: 500px;
  height: 40%;
  display: flex;
  flex-direction: column;
  Justify-content: space-between;
  margin-top: 100px;
}

.btn-next {
  margin-left: auto;
  text-align: center;
  color: white;
  background-color: #0C9267;

  width: 10%;
  font-size: 20px;
}

.percentage {
  position: absolute;
  background: #F26C5E;
  color: white;
  font-size: 24px;
  line-height: 28px;
}

.word-translation {
  margin-top: 40px;
  margin-bottom: 60px;
  font-size: 72px;
  line-height: 84px;
  color: #00402B;

}

.word-block {  
  position: relative;
  margin-top: 60px;
  font-weight: 300;
  font-size: 48px;
  line-height: 56px;
  color: #1AAD7E;
}

.word-container {
  display: flex;
  justify-content: center;

}

.description-label {
  position: absolute;
  font-size: 18px;
  line-height: 21px;
  color:gray;
}

.score {
/*   position: absolute;
  z-index: 4;
  left: 800px; */
  width: 120px;
  color: #036344;
  font-size: 24px;
  text-align: center;
  padding: 0 29px;
}

.check {
  background: #1AAD7E;
  border: 1px solid rgba(0, 0, 0, 0.03);
  box-sizing: border-box;
}

.library-container > div > div{
  padding: 15px;
  position: relative;
  
}

