.library-container > div {
  
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 20px;
  
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  color: #036344;
}

.library-container {
  background: rgba(242, 246, 254, 0.46);
  border: 1px solid rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
  margin-top: 15px;
  margin-right: 30px;
}

