.library-header > div {
  position: relative;
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 21px;

  color: #992E00;

}

.btn-repeat {
  margin: 30px auto;
  background-color: #0C9267;
  color: white;
  padding: 10px;
  border-radius: 50%;
  width: 85px;
  height: 85px;
  font-size: 40px;
  display: grid;
  justify-content: center;
  align-items: center;
}

.mode-container > div:first-child {
  display: grid;
  align-content: space-between;
  max-width: 280px;
}

.mode-container:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.mode-container {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  
  padding: 25px 55px 55px 55px;

}

.hard-mode {
  background: rgba(176, 222, 255, 0.46);
}

.easy-mode {
  background: rgba(215, 228, 255, 0.46);
}

.mode-title {
  font-weight: bold;
  font-size: 36px;
  line-height: 42px;

  color: #036344;
}

.mode-description {
  font-size: 24px;
  line-height: 28px;
}

.easy-mode .mode-description {
  color: #1881DF;
}

.hard-mode .mode-description {
  color: #1B46DF;
}


.mode-page {
  display: grid;
  align-items: start;
  margin: 130px auto;
}

.game-page {
  position: fixed;
  height: 100%;
  width: 100vw;
  top: 0;
  left: 0;
  background: rgb(242, 246, 254);
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.btn-exit {
  position: absolute;
  width: 175px;
  height: 50px;
  left: 42px;
  top: 32px;

  background: #036344;
  border: none;
  color: white;
  font-size: 24px;
}

.correct-title {
  font-size: 24px;
  line-height: 28px;
  margin-right: 15px;
  color: #006A31;
}

.error-title {
  font-size: 24px;
  line-height: 28px;

  color: #680000;
}

.points-block {
  position: absolute;
  left: 300px;
  top: 40px;
}

.mode-page a {
  text-decoration: none;
}

.mode-title-word-description {
  font-weight: 200;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 140px;
  color: #000000;
}

.mode-title-word {
  font-size: 144px;
  line-height: 169px;
  align-items: center;
  text-align: center;
  margin-top: 100px;

  color: #036344;
}

.check-item-block {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
}

.check-item {
  background: #FFFFFF;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 30px;
  font-size: 36px;
  line-height: 42px;
  text-align: center;

  color: #516961;
}

.mode-wraper {
  display: grid;
  align-items: start;
  max-width: 100%;
  height: 100%;
  justify-content: center;
  align-content: center;
  row-gap: 10px;
  text-align: center;

}

.btn-write-check {
  max-height: 30px;
}

.delete {
  cursor: pointer;
  display: flex;
  color: #960000;
}

.customInput {
  font-size: 20px;
  position: relative;
  border: none;
  background: transparent;
  padding: 8px 13px;
  border-bottom: 1px solid #084934;
}

.btn-enter {
  background-color: #0C9267;
  font-size: 26px;
  width: 143px;
  height: 46px;
  color: white;
  padding: 8px;
  border: none;
  margin-left: 35px;
}